header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 15px 0;
  z-index: 9999;
  transition: all 0.6s ease-in-out;
}
header .upper .number-wrapper {
  margin-right: 20px;
  margin-top: 10px;
}
header .upper .ctc-wrapper {
  margin-right: 20px;
}
header .upper .enq-btn-wrapper a {
  display: inline-block;
  padding: 0px 15px;
  color: #fff;
  background-color: #6b6b6b;
  text-transform: uppercase;
  line-height: 40px;
}
header .lower .nav-links {
  text-align: right;
}
header .lower .nav-links a {
  display: inline-block;
  margin-right: 20px;
  color: #4C4C4C;
  position: relative;
}
header .lower .nav-links a:last-child {
  margin-right: 0px;
}
header .lower .nav-links a.active {
  color: #003E63;
}
header .form-container {
  position: absolute;
  width: 300px;
  top: calc(100% + 10px);
  right: 15px;
  background-color: #efece7;
  padding: 10px;
  display: none;
}
header .form-container .frmclose {
  display: none;
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  color: #000;
  font-size: 20px;
}
header .form-container .thankyou {
  margin-bottom: 10px;
  margin-bottom: 10px;
}

header.active {
  background-color: #fff;
  z-index: 100000;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
}

.call-icon svg {
  width: 20px;
  height: 20px;
  position: relative;
  right: 2px;
  top: 4px;
}

.enquire-btn {
  background-color: green;
  color: #fff !important;
  font-size: 17px;
  padding: 14px 20px;
  font-weight: 500;
  line-height: 16px;
  border-radius: 4px;
  cursor: pointer;
}

.eq-height-row {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
}

footer {
  float: left;
  width: 100%;
  background-color: #000;
  color: #fff;
  padding: 50px 0;
}
footer a {
  color: #fff;
}
footer .container {
  max-width: 1160px;
  margin: 0px auto;
  padding: 0 15px;
}
footer .container .footerWrapper .partA {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 120px;
  padding-bottom: 35px;
  border-bottom: 1px solid #5A5A5A;
}
footer .container .footerWrapper .partA .content .title {
  font-weight: 650;
  font-size: 24px;
}
footer .container .footerWrapper .partA .content .para {
  font-weight: 450;
  font-size: 16px;
  line-height: 24px;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup {
  display: flex;
  font-weight: 450;
  font-size: 14px;
  color: #003E63;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup input::placeholder {
  font-weight: 450;
  font-size: 14px;
  color: #003E63;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .emailPassage {
  padding: 13px 18px;
  padding-right: 200px;
  background-color: #fff;
  margin: 0;
  border: 0px;
  border-radius: 6px 0 0 6px;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .submitBtn {
  padding: 13px 20px;
  padding-right: 40px;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #fff;
  border-left: 1px solid #003E63;
  border-radius: 0 6px 6px 0;
  position: relative;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .submitBtn::after {
  position: absolute;
  content: url("../images/rightVector.svg");
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
footer .container .footerWrapper .partB {
  padding: 35px 0;
  display: grid;
  grid-template-columns: 30% auto;
  align-items: center;
  gap: 0 50px;
}
footer .container .footerWrapper .partB .companyInfo .logo {
  margin-bottom: 15px;
}
footer .container .footerWrapper .partB .companyInfo .address {
  font-weight: 450;
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 20px;
}
footer .container .footerWrapper .partB .companyInfo .mail {
  font-weight: 350;
  font-size: 24px;
  display: flex;
  align-items: center;
  gap: 10px;
}
footer .container .footerWrapper .partB .footerNavs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 45px;
}
footer .container .footerWrapper .partB .footerNavs .cols {
  display: flex;
  flex-direction: column;
  gap: 15px 0;
}
footer .container .footerWrapper .partB .footerNavs .cols.ql .navLinks {
  grid-template-columns: repeat(2, 1fr);
  gap: 20px 30px;
}
footer .container .footerWrapper .partB .footerNavs .cols .title {
  font-weight: 600;
  font-size: 14px;
}
footer .container .footerWrapper .partB .footerNavs .cols .navLinks {
  display: grid;
  gap: 20px 0;
}
footer .container .footerWrapper .partB .footerNavs .cols .navLinks a {
  font-weight: 400;
  font-size: 14px;
}
footer .container .footerWrapper .partC {
  display: flex;
  justify-content: space-between;
}

/* ------------------------------ text animation code starts ---------------------------  */
.revealOnScroll {
  opacity: 0;
}

.animated {
  -moz-animation-duration: 1.2s;
  -o-animation-duration: 1.2s;
  -webkit-animation-duration: 1.2s;
  -ms-animation-duration: 1.2s;
  animation-duration: 1.2s;
  -moz-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  -webkit-animation-fill-mode: both;
  -ms-animation-fill-mode: both;
  animation-fill-mode: both;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -o-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    -o-transform: none;
    -moz-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
.fadeInDown {
  -webkit-animation-name: fadeInDown;
  -o-animation-name: fadeInDown;
  -ms-animation-name: fadeInDown;
  -moz-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  -o-animation-name: fadeInUp;
  -ms-animation-name: fadeInUp;
  -moz-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    -o-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    -moz-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    -o-transform: none;
    -ms-transform: none;
    -moz-transform: none;
    transform: none;
  }
}
.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  -o-animation-name: fadeInLeft;
  -ms-animation-name: fadeInLeft;
  -moz-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    -o-transform: translate3d(-100%, 0, 0);
    -moz-transform: translate3d(-100%, 0, 0);
    -ms-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    -moz-transform: none;
    -o-transform: none;
    transform: none;
  }
}
.fadeInRight {
  -webkit-animation-name: fadeInRight;
  -o-animation-name: fadeInRight;
  -ms-animation-name: fadeInRight;
  -moz-animation-name: fadeInRight;
  animation-name: fadeInRight;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    -o-transform: translate3d(100%, 0, 0);
    -ms-transform: translate3d(100%, 0, 0);
    -moz-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
